export default {
	data() {
		return {
			show: false,
			functions: [{
					id: 1,
					path: '../../static/passage2.png',
					des: '我的文章',
					detail: '0篇私密'
				},
				{
					id: 2,
					path: '../../static/Bookshelf.png',
					des: '我的书架',
					detail: '含已购内容'
				},
				{
					id: 3,
					path: '../../static/collection-outline.png',
					des: '喜欢收藏'
				},
				{
					id: 4,
					path: '../../static/Missionlist.png',
					des: '奖励任务',
					detail: '领简阅钻'
				}
			],
			storageData: {}
		};
	},
	onLoad() {

	},
	onShow: function() {
		var _this = this;
		const loginKey = uni.getStorageSync('login_key');
		console.log("come");
		if (loginKey) {
			console.log(loginKey);
			this.storageData = {
				login: loginKey.login,
				nickname: loginKey.nickname,
				avatar: loginKey.avatar,
			};

		} else {
			this.storageData = {
				login: false
			}
		};
		uni.request({
			url: 'http://localhost:8080/api/user/' + uni.getStorageSync('login_key').userId,
			method: 'GET',
			header: {
				'content-type': 'application/json'
			},
			success: res => {
				if (res.data.code === 0) {
					console.log(res.data.data + '————————————');
					this.storageData = {
						avatar: res.data.data.avatar,
						nickname: res.data.data.nickname,
						login: loginKey.login,
					};
				}
			}
		});
	},
	methods: {
		toMission: function(e) {
			if(e.id==4){
			uni.navigateTo({
				url: '../missioncenter/missioncenter',
			});
			}
		},
		toactivity: function() {
			uni.navigateTo({
				url: '../activity/activity',
			})
		},
		toWallet: function() {
			uni.navigateTo({
				url: '../mywallet/mywallet',
			})
		},
		showdialog: function() {
			this.show = true;
		},
		closedialog: function() {
			this.show = false;
		}
	}
}
